.button {
  display: block;
  width: 100px;
  height: 40px;

  border-radius: 20px;

  display: flex;
  justify-content: center;
  align-items: center;

  color: rgb(50, 123, 248);

  cursor: pointer;

  transition: all .1s;
}

.button:hover {
  background-color: whitesmoke;
  box-shadow: 1px 2px 4px rgba(0,0,0,.3);

}

.button--primary {
  color: whitesmoke;
  background-color: rgb(50, 123, 248);
}

.button--primary:hover {
  background-color: rgb(42, 103, 208);
}

.button--small {
  width: 80px;
  height: 30px;
  font-size: 12px;
}

.button--large {
  width: 200px;
  height: 50px;
  border-radius: 100px;
  font-size: 18px;

}

.button--disabled {
  color: white;
  background-color: grey;
  cursor: default;
}

.button--disabled:hover {
  color: white;
  background-color: grey;
  box-shadow: none;
}


.main-wrapper {
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 80px 1fr;
}

.main-content-wrapper {
  grid-row: 2 / 3;
  grid-column: 2 / 3;

  display: flex;
  justify-content: center;
  align-items: center;

}
