.form-input__container {
  position: relative;

  box-sizing: border-box;
  width: 300px;
  height: 40px;

  border: 1px solid rgba(0,0,0,.3);
  border-radius: 20px;

  padding: 10px 20px;
  margin: 10px;

  cursor: text;
}

.form-input__name {
  z-index: 25;
  position: absolute;
  top: 7px;
  left: 20px;
  text-align: center;

  background-color: white;
  padding: 5px;

  transition: all .3s;

}

.form-input__input{
  position: absolute;

  width: 85%;

  border: none !important;

  font-size: 1.1em;

  outline: none;

  text-overflow: ellipsis;
}

.form-input__container--focused {
  border: 2px solid rgb(42, 103, 208);
}

.form-input__container--filled .form-input__name {
  top:-11px;
  font-size: 12px;
  transition: all .3s;
}
